import {createGlobalStyle} from "styled-components";

export const GlobalStyles = createGlobalStyle`
    html {
        box-sizing: border-box;
    }
    
    *, *::before, *::after {
        box-sizing: inherit;
        margin: 0;
        padding: 0;
    }
    
    body {
        color: #121212;
        font-family: "Roboto", sans-serif;
        font-weight: 400;
        background-color: #F0F0F0;
        overflow: hidden;
    }
    
    a {
        text-decoration: none;
    }
`;